import React from "react"
import businessdata from  "./business.json"
import Businessdetails from "./Businessdetails.mrt"
class BusinessReport extends React.Component {

    componentDidMount() {
        console.log('Loading Viewer view');
    
        console.log('Creating the report viewer with default options');
        var viewer = new window.Stimulsoft.Viewer.StiViewer(null, 'StiViewer', false);

        console.log('Creating a new report instance');
        var report = new window.Stimulsoft.Report.StiReport();

        console.log('Load report from url');
        report.loadFile(Businessdetails);
        report.dictionary.databases.getByIndex(0).pathData =businessdata;
        console.log('Assigning report to the viewer, the report will be built automatically after rendering the viewer');
        viewer.report = report;

        console.log('Rendering the viewer to selected element');
        viewer.renderHtml('viewer');
    }

    render() {
        
            return <div id="viewer"></div>;
        
    }
}

export default BusinessReport
